//公共组件控制 store 
import * as types from '@/store/mutations-types';

const state = {
	layerFlag: false, // layer组件显示隐藏

	spinWheelFlag: false, // spinWheel组件显示隐藏
	spinWheelOptions: {}, // spinWheel组件数据
	spinWheelResult: [], // spinWheel组件结果

}


const actions = {
	changeLayerState({ //layer组件显示隐藏
		commit
	}) {
		return commit(types.LAYER_STATECHANGE);
	},
	changeSpinWheelState({ //spinWheel组件显示隐藏
		commit
	}) {
		return commit(types.SPINWHEEL_STATECHANGE);
	},

	setSpinWheelOptionsDatas({
		commit
	}, spinWheelOptions) {
		return commit(types.SPINWHEEL_OPTIONDATAS, spinWheelOptions);
	},

	setSpinWheelResult({
		commit
	}, spinWheelResult) {
		return commit(types.SPINWHEEL_RESULT, spinWheelResult);
	},

}
const mutations = {
	[types.LAYER_STATECHANGE](state) { //layer组件显示隐藏
		state.layerFlag = !state.layerFlag;
	},
	[types.SPINWHEEL_STATECHANGE](state) { //spinWheel组件显示隐藏
		state.spinWheelFlag = !state.spinWheelFlag;
	},
	[types.SPINWHEEL_OPTIONDATAS](state, spinWheelOptions) {
		state.spinWheelOptions = spinWheelOptions;
	},
	[types.SPINWHEEL_RESULT](state, spinWheelResult) {
		state.spinWheelResult = spinWheelResult;
	},
}

export default {
	namespaced: true, //* 命名空间
	state,
	actions,
	mutations
}


