import axios from './axios'

//* 获取随机数
export function getRandomNumber() {

	console.log("axios api getRandomNumber");

	var biz_no = "loan" + Date.parse(new Date());
	var api_key = "sGiRNxDi-jiLAK_JtZrt3guCRK-1mYxy";
	var api_secret = "41dQ0Leyl6yvPhunwZex64u3K_JLPRsW";

	var formdata = ObjectTOString({
		"api_key": api_key,
		"api_secret": api_secret,
		"biz_no": biz_no,
	});


	return axios.post('https://api.megvii.com/faceid/lite/raw/get_random_number', formdata)
}

function ObjectTOString(validateData) {

	let bodyCluster = "";
	let bodyParasArray = [];

	//对象转数组 对象可以包含一层数组 不支持多层数据
	let indexOfKey = 0;
	for (let key in validateData) {

		if (validateData[key] instanceof Array) {
			let bodyCluster = "";
			validateData[key].forEach(function(item, i, a) {
				if (i != 0) {
					bodyCluster = bodyCluster + "&" + key + "=" + item;
				} else {
					bodyCluster = key + "=" + item;
				}
			});

			indexOfKey++;
			bodyParasArray[indexOfKey] = bodyCluster;
		} else {
			indexOfKey++;
			bodyParasArray[indexOfKey] = key + "=" + validateData[key];
		}

	}
	//数组排序
	bodyParasArray.sort();

	//拼接字符串
	bodyParasArray.map(function(item, i) {
		if (i != 0) {
			bodyCluster = bodyCluster + "&" + item;
		} else {
			bodyCluster = bodyCluster + item;
		}
	});

	return bodyCluster;
}


