//路由控制store
const state = {
	pageDirection: 'fade',
	routeChain: []
}


const mutations = {
	addRouteChain(state, route) {
		state.routeChain.push(route);
	},
	popRouteChain(state) {
		state.routeChain.pop();
	},
	setPageDirection(state, dir) {
		state.pageDirection = dir;
	}
}

export default {
	namespaced: true, //* 命名空间
	state,
	mutations
}


