// The Vue build version to load with the `import` command 
import Vue from 'vue'
import store from '@/store'
import router from './router'
import App from './App'

import {
	sync
} from 'vuex-router-sync'

import axios from '@/api/axios.js'

//全局属性
Vue.prototype.$axios = axios;

//同步路由
sync(store, router)

Vue.config.productionTip = false

//* 路由拦截
router.beforeEach((to, from, next) => {

	let routeLength = store.state.routeCtro.routeChain.length;
	if (routeLength === 0) {
		// console.log(" 前进");
		store.commit('routeCtro/setPageDirection', 'fade');
		if (to.path === from.path && to.path === '/') {
			//当直接打开根路由的时候
			store.commit('routeCtro/addRouteChain', to);
		} else {
			//直接打开非根路由的时候其实生成了两个路径，from其实就是根路由
			store.commit('routeCtro/addRouteChain', "/");
			store.commit('routeCtro/addRouteChain', to);
		}
	} else if (routeLength === 1) {
		// console.log(" 前进");
		store.commit('routeCtro/setPageDirection', 'slide-left');
		store.commit('routeCtro/addRouteChain', to);
	} else {
		let lastBeforeRoute = store.state.routeCtro.routeChain[routeLength - 2];
		if (lastBeforeRoute.path === to.path) {
			// console.log(" 后退");
			store.commit('routeCtro/popRouteChain');
			store.commit('routeCtro/setPageDirection', 'slide-right');
		} else {
			// console.log(" 前进");
			store.commit('routeCtro/addRouteChain', to);
			store.commit('routeCtro/setPageDirection', 'slide-left');
		}
	}
	next();

})


/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	store,
	template: '<App/>',
	components: {
		App
	}
})


