//第一页 store

import * as types from '@/store/mutations-types';

import {
	getRandomNumber
} from '@/api/api'

const state = {
	randomNum: ""
}

const actions = {
	fetchRandomNum: ({
		commit
	}) => new Promise((reslove, reject) => {
		getRandomNumber().then(response => {
			if (response.data.code === 'ERROR') {
				//这个是返回的错误数据
			} else {
				commit(types.SET_RANDOMNUM, response.data.token_random_number);
				reslove();
			}
		}).catch(err => {
			// 其实这个地方不一定是接口挂了 

			// 接口正常返回 但是在response的处理过程中出错
			reject("网络出现问题 处理出错");
		})
	})
}

const mutations = {
	[types.SET_RANDOMNUM](state, randomNum) {
		state.randomNum = randomNum;
	}
}

export default {
	namespaced: true, //* 命名空间
	state,
	actions,
	mutations
}


