 const state = {
 	count: 10,
 	numb: 10086
 }


 const actions = {
 	actionA({
 		commit
 	}) {
 		return commit('increment');
 	},
 }

 const mutations = {
 	increment(state) {
 		state.count += 2;
 		state.numb += 2;
 	},
 }

 export default {
 	namespaced: true, //* 命名空间
 	state,
 	actions,
 	mutations
 }


