import Vue from 'vue'
import Router from 'vue-router'

//views
const firstPage = reslove => require(['@/views/firstPage/firstPage.vue'], reslove);

const secondPage = reslove => require(['@/views/secondPage/secondPage.vue'], reslove);

const thirdPage = reslove => require(['@/views/thirdPage/thirdPage.vue'], reslove);
const fourPage = reslove => require(['@/views/fourPage/fourPage.vue'], reslove);

const fivePage = reslove => require(['@/views/fivePage/fivePage.vue'], reslove);

const notFound = reslove => require(['@/views/notFound/notFound.vue'], reslove);


//components
const componentA = reslove => require(['@/components/componentA/componentA.vue'], reslove);


//* 默认加载路由
const defaultRouter = [{
    path: '/',
    name: 'firstPage',
    component: firstPage,
}, {
    path: '/secondPage',
    name: 'secondPage',
    component: secondPage
}, {
    path: '/thirdPage',
    name: 'thirdPage',
    component: thirdPage,
    children: [{
        path: '/thirdPage',
        name: 'componentA',
        component: componentA
    }]
}, {
    path: '/fourPage',
    name: 'fourPage',
    component: fourPage
}, {
    path: '/fivePage',
    name: 'fivePage',
    component: fivePage,
}, {
    path: '*',
    component: notFound
}];


Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: defaultRouter
})


